<template>
  <view class="my-settle-container" >
    <!-- 全选 -->
      <label class="radio" @click="changeAllState">
        <radio color="#C00000" :checked="isAllTotal" /><text>全选</text>
      </label>
      <!-- 合计 -->
      <view class="amount-box">
        合计: <text class="amount">￥{{checkedGoodsAmount}}</text>
      </view>
      <!-- 结算 -->
      <view class="btnsettle">结算({{checkedCount}})</view>
  </view>
</template>

<script>
  import {mapGetters,mapMutations} from 'vuex'
  export default {
    name:"my-settle",
    computed:{
      ...mapGetters('m_cart',['checkedCount','total','checkedGoodsAmount']),
      isAllTotal(){
        return this.total === this.checkedCount
      }
    },
    data() {
      return {
        
      };
    },
    methods:{
      ...mapMutations('m_cart',['updateAddGoodsState']),
      changeAllState(){
        this.updateAddGoodsState(!this.isAllTotal)
      }
    }
  }
</script>

<style lang="scss">
  
.my-settle-container{
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  padding-left: 5px;
  background-color: white;
  .radio{
    display: flex;
    align-items: center;
  }
  .amount-box{
    color: #C00000;
  }
  .btnsettle{
    background-color: #C00000;
    height: 100%;
    line-height: 50px;
    color: white;
    padding: 0 10px;
    min-width: 100px;
    text-align: center;
  }
}
</style>